<template>
  <div class="Players">
    <div
      v-for="player in players"
      :key="timestamp(player)"
      :style="getStyle(player)"
      class="player"
    >
      <div class="health">
        {{ player.health }}
      </div>
    </div>
  </div>
</template>

<script>
let saveTimestamp = 0;

export default {
  name: 'Players',

  data() {
    return {
      players: {},
    };
  },

  mounted() {
    window.app.connection.on('event', (datas) => {
      delete datas[window.app.id];
      this.players = datas;
    });
  },

  methods: {
    timestamp(player) {
      saveTimestamp += 1;
      return (player.id && this.players[player.id].timestamp) || saveTimestamp;
    },

    getStyle(player) {
      // todo из за того, что на сервере есть пустые клиенты.
      if (Object.keys(player).length > 0) {
        const { health, position: { left, top }, scale, isCrossed } = player;
        const color = this.getHealthColor(health);

        return {
          transform: `translate(${left}px, ${top}px) scale(${scale})`,
          backgroundColor: isCrossed ? 'darkred' : color,
        };
      }
    },

    getHealthColor(health) {
      let color = '';

      switch (true) {
        case health > 150:
          color = 'hsl(120, 80%, 40%)';
          break;

        case health > 100:
          color = 'hsl(120, 80%, 30%)';
          break;

        case health > 50:
          color = 'hsl(120, 80%, 20%)';
          break;

        case health > 0:
          color = 'hsl(120, 80%, 10%)';
          break;

        default:
          color = 'black';
      }

      return color;
    },
  },
};
</script>

<style scoped>
.Players {
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.player {
  position: absolute;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  transition: background-color 0.1s;
  will-change: transform;
}

.health {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 1.4rem;
  font-weight: bold;
  color: white;
  will-change: contents;
}
</style>
